.App{
      
}

.PortfollioLaptop{
      width: 80%;
      margin: auto;
}

.PortfollioMobile{
      display: none;
      margin-top: 60px;
}
.NavbarLaptop{
      box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}
.NavbarMobile{
      box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
      display: none;
}

@media screen and (max-width: 420px){
      .PortfollioLaptop{
            display: none;
      }

      .PortfollioMobile{
            display: block;
      }
      .NavbarLaptop{
            display: none;
      }
      .NavbarMobile{
            display: block;
      }
}
